<template>
    <div justify="space-around" align="center">
    <br/>
    <div>
    <v-btn
        class="mx-2"
        
        @click="viewUpiti"
    >
        Upiti
    </v-btn>

     <v-btn
        class="mx-2"
        @click="viewNarudzbenice"
    >
        Narudbenice
    </v-btn>

     <v-btn
        class="mx-2"
        color="light-green"
        @click="viewDodajNarudzbenicu"
    > 
        <v-icon>
            mdi-plus
        </v-icon>
        Nova narudbenica 
    </v-btn>
    </div>
    <br/>
    <br/>

    
    
    <v-container fluid v-if="showUpiti">
        <TableUpiti :apotekaId="apotekaId"/>
      </v-container>

    <v-container fluid v-if="showNarudzbenice">
        <TableNarudzbenice :apotekaId="apotekaId" :userId="this.user.id"/>
      </v-container>

    <v-container fluid v-if="showNovaNarudzbenica">
        <NovaNarudzbenica :apotekaId="apotekaId" :user="this.user"/>
      </v-container>
   

    <v-dialog
      v-model="obavestenjeDialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">
          Obavetenje
        </v-card-title>
        <v-card-text>Nije pronaen farmaceut sa zadatim parametrima.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            @click="endObavestenjeDialog"
          >
            Ok
          </v-btn>
        </v-card-actions>
      </v-card>
  </v-dialog>
    </div>
    
</template>

<script>
import TableUpiti from "./TableUpiti";
import TableNarudzbenice from "./TableNarudzbenice";
import NovaNarudzbenica from "./NovaNarudzbenica";


export default{
    name: "NarucivanjeAdminApoteke",
    components: {
        TableUpiti,
        TableNarudzbenice,
        NovaNarudzbenica
    },
    data: () => ({
        
        obavestenjeDialog: false,
        showNarudzbenice:false,
        showUpiti:true,
        showNovaNarudzbenica:false,
    }),
    props :{
        user: {},
        apotekaId: Number,
    },

    methods:{
        endObavestenjeDialog(){
            this.obavestenjeDialog = false;
        },
        viewUpiti(){
            this.showUpiti = true;
            this.showNarudzbenice = false;
            this.showNovaNarudzbenica = false;
        },
        viewNarudzbenice(){
            this.showUpiti = false;
            this.showNarudzbenice = true;
            this.showNovaNarudzbenica = false;
        },
        viewDodajNarudzbenicu(){
            this.showUpiti = false;
            this.showNarudzbenice = false;
            this.showNovaNarudzbenica = true;
        }
        
        
    },
}
</script>

